import '../styles/tokens-grid.css'

interface TokensGridProps {
    tokens: Object
    hasRem: boolean
}

export function TokensGrid({ tokens, hasRem = false }: TokensGridProps){
    return(
        <table className="tokens-grid">
            <thead>
                <tr>
                    <th>
                        Name
                    </th>
                    <th>
                        Value
                    </th>
                    { hasRem && <th> Value in Px </th>}
                </tr>
            </thead>
            <tbody>
                {
                    Object.entries(tokens).map(([key, value]) => {
                        return (
                            <tr key={key}>
                                <td> { key } </td>
                                <td> { value } </td>
                                { hasRem && <td> {Number(value.replace('rem', '') * 16)} </td>}
                            </tr>
                        );
                    })
                }
            </tbody>
        </table>
    );
}